import React, { Component } from 'react';
import './BurgerStyle.css';

 export default class Burger extends Component {
  state = {
    lettuce: 0,
    tomato: 0,
    cheese: 0,
    meat: 0,
  };

  addRemoveIngredient = (action, ingredient) => {
    let { lettuce, tomato, cheese, meat } = this.state;

    let stateValue;
    switch (ingredient) {
      case "lettuce": {
        stateValue = lettuce;
        break;
      }
      case "tomato": {
        stateValue = tomato;
        break;
      }
      case "cheese": {
        stateValue = cheese;
        break;
      }
      case "meat": {
        stateValue = meat;
        break;
      }
      default:
        break;
    }
    if (action === "add") {
      stateValue = stateValue + 1;
    } else {
      stateValue = stateValue - 1;
    }
    this.setState({
      [ingredient]: stateValue >= 0 ? stateValue : 0,
    });
  };

  burgerContent = () => {
    let { lettuce, tomato, cheese, meat } = this.state;
    let burger = [];

    for (let i = 0; i < lettuce; i++) {
      burger.push(<div key={burger.length} className="lettuseSide"></div>);
    } 
    for (let i = 0; i < tomato; i++) {
      burger.push(<div key={burger.length} className="tomatoSide"></div>);
    }
    for (let i = 0; i < cheese; i++) {
      burger.push(<div key={burger.length} className="cheeseSide"></div>);
    }
    for (let i = 0; i < meat; i++) {
      burger.push(<div key={burger.length} className="meatSide"></div>);
    }
    return burger;
  };

  totalSumma = () => {
    let { lettuce, tomato, cheese, meat } = this.state;
    let total = 0;
    for (let i = 0; i < lettuce; i++) {
      total += 0.1;
    }
    for (let i = 0; i < tomato; i++) {
      total += 0.4;
    }
    for (let i = 0; i < cheese; i++) {
      total += 0.6;
    }
    for (let i = 0; i < meat; i++) {
      total += 1.2;
    }
    return total.toFixed(1);
  };

  
  render() {
    return (
      <>
        <div className="total">Umumiy hisob:{this.totalSumma()}$</div>
        <div className="parent">
          <div className="ingredientsBlock">
            <p>
              Ko'katlar <i className="crimson">0.1$</i>
            </p>
            <div className="ingrBtns">
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("add", "lettuce")}
              >
                Qo'shish
              </button>
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("remove", "lettuce")}
              >
                o'chirish
              </button>
            </div>
            <p>
              Pomidor <i className="crimson">0.4$</i>
            </p>
            <div className="ingrBtns">
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("add", "tomato")}
              >
                qo'shish
              </button>
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("remove", "tomato")}
              >
                o'chirish
              </button>
            </div>
            <p>
              Pishloq <i className="crimson">0.6$</i>
            </p>
            <div className="ingrBtns">
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("add", "cheese")}
              >
                qo'shish
              </button>
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("remove", "cheese")}
              >
                o'chirish
              </button>
            </div>
            <p>
              Go'sh <i className="crimson">1.2$</i>
            </p>
            <div className="ingrBtns">
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("add", "meat")}
              >
                qo'shish
              </button>
              <button
                className="ingrBtn"
                onClick={() => this.addRemoveIngredient("remove", "meat")}
              >
                o'chirish
              </button>
            </div>
          </div>
          <div className="burgerIngredients">
            <div className="topSide">
              <div className='parentSedana'>
              <div className='sedana sedana1'></div>
              <div className='sedana sedana2'></div>
              <div className='sedana sedana3'></div>
              <div className='sedana sedana2'></div>
              <div className='sedana sedana3'></div>
              <div className='sedana sedana1'></div>
              <div className='sedana sedana2'></div>
              <div className='sedana sedana2'></div>
              <div className='sedana'></div>
              <div className='sedana sedana3'></div>
              </div>
            </div>
            {this.burgerContent()}     
            <div className="bottomSide"></div>
          </div>
        </div>
      </>
    );
  }
}

